import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class SpeechService {
  recognition;

  static isVoiceOverOn = false;
  static isTalking = false;

  constructor() {
    const SpeechRecognition =
      // @ts-ignore
      window.SpeechRecognition || window.webkitSpeechRecognition;

    this.recognition = new SpeechRecognition();

    document.body.addEventListener('click', () => {
      if (!SpeechService.isVoiceOverOn || SpeechService.isTalking) return;

      this.recognition.start();
    });

    this.recognition.onresult = event => {
      const message: string = event.results[0][0].transcript;

      if (message.toLowerCase().includes('market')) {
        this.read(
          'USD/CNY Spike Detected on 17th of December 2019. EUR/GBP Missing Price 2019-12-17.'
        );
      } else if (message.toLowerCase().includes('static')) {
        this.read(
          'CUST1 Swift code missing; LEI missing. Instrument1 ISIN malformed: US00040262.'
        );
      } else if (message.toLowerCase().includes('barrier')) {
        this.read('EUR/USD Barrier Breached.');
      } else if (message.toLowerCase().includes('sentiment')) {
        this.read('Stock1 negative');
      } else if (message.toLowerCase().includes('position')) {
        this.read('Position P&L exceeds tolerance 10%.');
      } else if (message.toLowerCase().includes('breaches')) {
        this.read('There are no Compliance Breaches alerts');
      } else if (message.toLowerCase().includes('customers')) {
        this.read('There are no Customers KYC alerts');
      }
    };
  }

  readNotifications() {}

  read(message, onSpeechEnd = () => {}) {
    SpeechService.isTalking = true;

    const speech = new SpeechSynthesisUtterance();

    // Set the text and voice attributes.
    speech.text = message;
    speech.volume = 1;
    speech.rate = 1.1;
    speech.pitch = 1;

    window.speechSynthesis.speak(speech);

    speech.addEventListener('end', () => {
      SpeechService.isTalking = false;
      onSpeechEnd();
    });
  }
}
